/* css - skills portfólio */
.skills-bg {
    background: var(--color-e2);
}

.skills-titulo {
    display: flex;
    justify-content: center;
    padding: 3.75rem 1.25rem 1.125rem 1.25rem;
}

.skills-content {
    display: flex;
    justify-content: center;
    gap: 7.5rem;
    padding-bottom: 3.75rem;
}

.skills-item h2 {
    font: 400 2rem/1.3 var(--font-title2);
    color: var(--color-b5);
    margin-bottom: 1.25rem;
}

.skills-lista {
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
}

.skills-lista li {
    font: 400 1rem/1.5 var(--font-text);
    color: var(--color-b8);
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid var(--color-b5);
}

.skills-lista li span {
    color: var(--color-b5);
    font-size: 0.875rem;
}

@media(width <=1000px) {
    .skills-content {
        gap: 4rem;
    }
}

@media(width <=780px) {
    .skills-content {
        flex-direction: column;
        padding-bottom: 2rem;
        gap: 2rem;
    }

    .skills-item {
        padding: 0px 4rem;
    }
}

@media(width <=780px) {
    .skills-item h2 {
        font-size: 1.5rem;
    }

    .skills-item {
        padding: 0px 2rem;
    }
}